html,
body {
  height: 100%; }

html {
  -webkit-font-smoothing: antialiased;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  scrollbar-color: rgba(255, 255, 255, 0.3) rgba(46, 43, 57, 0.3);
  overflow-x: hidden; }
  html::-webkit-scrollbar {
    width: 10px; }
  html::-webkit-scrollbar-thumb {
    background-color: rgba(46, 43, 57, 0.3);
    border-radius: 0; }

.body {
  color: #404040;
  font-size: 16px;
  text-rendering: optimizeSpeed;
  font-family: Arial, Helvetica, sans-serif; }

*:focus,
button:focus {
  outline: none; }

button:focus {
  box-shadow: none !important; }

button {
  transition: color 0.3s, border-color 0.3s, transform 0.3s, background-color 0.3s !important; }

svg {
  transition: fill 0.3s, filter 0.3s, stroke 0.3s; }

a {
  position: relative;
  color: #834700;
  text-decoration: none;
  transition: color 0.3s, background-color 0.3s, border-color 0.3s !important; }
  a:hover {
    text-decoration: none;
    color: #404040; }
  a:focus {
    text-decoration: none;
    color: #404040;
    box-shadow: none !important; }

a:not([class]) {
  text-decoration-skip-ink: auto; }

.break:after {
  content: "\A";
  white-space: pre; }

input,
button,
textarea,
select {
  font: inherit; }

input:focus::placeholder,
textarea:focus::placeholder {
  color: transparent; }

img,
video,
audio {
  max-width: 100%;
  height: auto; }

sub,
sup {
  line-height: 0; }

label,
button,
select {
  cursor: pointer; }

input {
  cursor: text; }

[disabled] {
  cursor: default; }

abbr[title],
acronym[title],
dfn {
  cursor: help; }

textarea {
  resize: vertical; }

input,
textarea {
  background-clip: padding-box; }

p:last-child {
  margin-bottom: 0; }

.sr-only {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

.mobile {
  display: none !important; }
  @media (max-width: 767px) {
    .mobile {
      display: inline-block !important; } }

@media (max-width: 767px) {
  .desktop {
    display: none !important; } }

.center {
  text-align: center; }

.btn {
  padding: 15px 10px;
  font-weight: 700;
  text-transform: uppercase; }
  .btn_border {
    border: 2px solid #834700; }
    .btn_border:hover {
      color: #6a3900;
      border-color: #6a3900; }
    .btn_border:focus {
      color: #6a3900;
      border-color: #6a3900; }
    .btn_border:active {
      opacity: 0.3;
      color: #6a3900;
      border-color: #6a3900; }
  .btn_primary {
    background-color: #834700;
    color: #fff; }
    .btn_primary:hover {
      color: #fff;
      background-color: #6a3900; }
    .btn_primary:focus {
      color: #fff;
      background-color: #6a3900; }
    .btn_primary:active {
      color: rgba(255, 255, 255, 0.3); }

.container {
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px; }
  .container_small {
    max-width: 800px; }
  .container_big {
    max-width: 1015px; }

.header {
  padding-top: 60px;
  background-image: url(/images/wheat-back.jpg);
  background-size: cover;
  background-position: bottom;
  height: 550px;
  color: #fff; }
  .header__inner {
    height: 100%;
    display: flex;
    flex-direction: column; }
  .header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .header__promo {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center; }
  .header__title {
    font-size: 70px;
    font-weight: 700; }

.menu {
  width: 75%;
  border-top: 1px solid rgba(255, 255, 255, 0.2); }
  .menu__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
    justify-content: space-between; }
  .menu__item {
    flex-grow: 1;
    text-align: center; }
  .menu__link {
    display: block;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    padding: 20px 0;
    color: currentColor;
    text-transform: uppercase;
    font-weight: 700; }
    .menu__link:hover {
      border-bottom-color: #fff;
      color: #ffcb78; }
    .menu__link:focus {
      color: currentColor; }
    .menu__link:active {
      color: rgba(255, 203, 120, 0.3); }

.products__items {
  display: flex; }

.products__item {
  width: 50%;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 50px; }

.products__name {
  margin-bottom: 40px;
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  color: #000; }

.products__wrapper {
  position: relative;
  margin-bottom: 65px; }

.products__desc {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 50%; }

.products__info {
  display: table;
  min-width: 150px;
  max-width: 150px;
  margin-bottom: 15px;
  border: 1px solid #e5e5e5;
  border-collapse: collapse;
  color: #000; }
  .products__info-item {
    display: table-row; }
  .products__info-prop, .products__info-value {
    display: table-cell;
    border: 1px solid #e5e5e5;
    font-weight: 700;
    padding: 10.5px 5px; }
  .products__info-prop {
    text-transform: uppercase; }

.products__composition {
  max-width: 150px;
  font-size: 14px;
  line-height: 24px; }

.products__text {
  margin-bottom: 65px;
  line-height: 30px; }

.products__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.products__buttons {
  margin-left: -7px; }
  .products__buttons .btn {
    margin-left: 7px; }

.products__price {
  font-weight: 700;
  color: #000; }

.section {
  padding-top: 90px;
  padding-bottom: 40px; }
  .section__title {
    margin-bottom: 80px;
    font-size: 40px;
    font-weight: 700;
    text-transform: uppercase;
    color: #000; }
